import React, { Component } from "react";
import { FaGithub, FaTwitter, FaLinkedin } from 'react-icons/fa';

export default class Header extends Component{
  render(){
    return(
      <React.Fragment>
        <header className="header">
          <div className="logo-contianer">
            <a href="index.html">
              <img className="logo" src="images/logo-v2.svg" alt="Homepage" />
            </a>
          </div>
          <nav className="header-nav">
            <ul className="header-main-nav">
              <li><a className="nav-item" href="#intro" title="intro">Intro</a></li>
              <li><a className="nav-item" href="#skills" title="services">Skills</a></li>
              <li><a className="nav-item" href="#experience" title="works">Experience</a></li>
              <li><a className="nav-item" href="#contact" title="contact us">Say Hello</a></li>
            </ul>
            <ul className="header-social">
              <li className="header-icons"><a href="https://github.com/NumbSkullq1"><FaGithub /></a></li>
              <li className="header-icons"><a href="https://www.twitter.com/numbskullq1"><FaTwitter /></a></li>
              <li className="header-icons"><a href="https://www.linkedin.com/in/aamirdammad"><FaLinkedin /></a></li>
              </ul>
          </nav>
          <a className="header-menu-toggle" href="#"><span>Menu</span></a>
        </header>
      </React.Fragment>
    );
  }
}
